"use client";

import React, { useEffect, useRef, useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { MapPin, Phone, Mail, Send, CheckCircle } from "lucide-react";
import { cn } from "@/lib/utils";
import { branches, contactInfo, Branch } from "@/lib/contact-data";

/* =====================
   Branch Selector
===================== */
function BranchSelector() {
  const [selectedBranch, setSelectedBranch] = useState<Branch>(branches[0]);

  return (
    <div className="space-y-5">
      <div className="space-y-2">
        <Label className="text-sm font-semibold text-white/90">
          Select Branch
        </Label>

        <select
          className="w-full h-12 bg-white/5 border border-white/10 text-white rounded-lg px-4 focus:ring-2 focus:ring-[#C5A55A]/50"
          value={selectedBranch.id}
          onChange={(e) => {
            const branch = branches.find((b) => b.id === e.target.value);
            if (branch) setSelectedBranch(branch);
          }}
        >
          {branches.map((branch) => (
            <option
              key={branch.id}
              value={branch.id}
              className="bg-[#0B1D3A] text-white"
            >
              {branch.name}
            </option>
          ))}
        </select>
      </div>

      <div className="space-y-4 p-5 rounded-xl border border-white/10 bg-white/5">
        <InfoRow icon={MapPin} label="Address" value={selectedBranch.address} />
        <InfoRow icon={Phone} label="Phone" value={selectedBranch.phone} />
        <InfoRow icon={Mail} label="Email" value={selectedBranch.email} />
      </div>
    </div>
  );
}

function InfoRow({
  icon: Icon,
  label,
  value,
}: {
  icon: any;
  label: string;
  value: string;
}) {
  return (
    <div className="flex gap-3">
      <div className="h-10 w-10 flex items-center justify-center rounded-lg border border-white/10 bg-white/5">
        <Icon className="h-4 w-4 text-[#C5A55A]" />
      </div>
      <div>
        <p className="text-xs font-bold tracking-wider text-[#C5A55A] uppercase">
          {label}
        </p>
        <p className="text-sm text-white/80">{value}</p>
      </div>
    </div>
  );
}

/* =====================
   Contact Section
===================== */
export default function ContactSection() {
  const ref = useRef<HTMLDivElement>(null);
  const [isVisible, setIsVisible] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => entry.isIntersecting && setIsVisible(true),
      { threshold: 0.1 }
    );
    ref.current && observer.observe(ref.current);
    return () => observer.disconnect();
  }, []);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitted(true);
    setTimeout(() => setIsSubmitted(false), 4000);
  };

  return (
    <section
      id="contact"
      className="relative py-24 bg-[#0B1D3A] overflow-hidden"
    >
      <div
        ref={ref}
        className={cn(
          "max-w-7xl mx-auto px-6 transition-all duration-700",
          isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-8"
        )}
      >
        {/* Header */}
        <div className="mb-12 max-w-2xl">
          <span className="text-sm font-semibold tracking-widest text-[#C5A55A] uppercase">
            Contact Us
          </span>
          <h2 className="mt-3 text-4xl font-serif font-bold text-white">
            Get In Touch
          </h2>
          <p className="mt-4 text-white/60">
            Have a project in mind? Reach out to discuss your requirements.
          </p>
        </div>

        <div className="grid lg:grid-cols-5 gap-14">
          {/* Form */}
          <div className="lg:col-span-3">
            <form
              onSubmit={handleSubmit}
              className="space-y-6 rounded-2xl border border-white/10 bg-white/5 p-8"
            >
              <div className="grid sm:grid-cols-2 gap-6">
                <InputBlock label="Full Name" placeholder="Your name" />
                <InputBlock
                  label="Email Address"
                  type="email"
                  placeholder="you@company.com"
                />
              </div>

              <div className="grid sm:grid-cols-2 gap-6">
                <InputBlock label="Phone Number" placeholder="+91 XXXXX XXXXX" />
                <InputBlock
                  label="Service Required"
                  placeholder="Curtain Wall, Glazing"
                />
              </div>

              <div>
                <Label className="text-sm font-semibold text-white/90">
                  Project Details
                </Label>
                <Textarea
                  required
                  rows={5}
                  className="bg-white/5 border-white/10 text-white"
                  placeholder="Tell us about your project..."
                />
              </div>

              <Button
                type="submit"
                disabled={isSubmitted}
                className="bg-[#C5A55A] text-white rounded-full px-10"
              >
                {isSubmitted ? (
                  <>
                    <CheckCircle className="mr-2 h-4 w-4" />
                    Enquiry Sent
                  </>
                ) : (
                  <>
                    <Send className="mr-2 h-4 w-4" />
                    Send Enquiry
                  </>
                )}
              </Button>
            </form>
          </div>

          {/* Info */}
          <div className="lg:col-span-2 space-y-8">
            <div className="rounded-2xl border border-white/10 bg-white/5 p-8">
              <h3 className="text-xl font-bold text-white mb-6">
                Contact Information
              </h3>

              <div className="space-y-6">
                {contactInfo.map((item) => (
                  <InfoRow
                    key={item.label}
                    icon={item.icon}
                    label={item.label}
                    value={item.value}
                  />
                ))}
              </div>
            </div>

            <div className="rounded-2xl border border-white/10 bg-white/5 p-8">
              <h3 className="text-xl font-bold text-white mb-6">
                Our Locations
              </h3>
              <BranchSelector />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

/* =====================
   Reusable Input
===================== */
function InputBlock({
  label,
  ...props
}: {
  label: string;
  [key: string]: any;
}) {
  return (
    <div className="space-y-2">
      <Label className="text-sm font-semibold text-white/90">{label}</Label>
      <Input
        {...props}
        required
        className="h-12 bg-white/5 border-white/10 text-white"
      />
    </div>
  );
}
